<template>
    <div class="classif-wrap">
        <div v-title data-title="昨日尾单">昨日尾单</div>
        <header  class="header">昨日尾单</header>
        <div class="hotGoods-wrap">
            <ul class="content-main clearfix">
                <li class="fl" v-for="(item,index) in data" :key="item.id">
                    <div class="image"  @click="tolink(index)">
                        <img v-if="item.pic_url" v-lazy="URL + item.pic_url">
                        <img v-if="item.stock == 0" class="sellout_img" src="../../assets/sellout2.jpg" alt="">
                    </div>
                    <div style="height:1.2rem">
                        <p class="desi">{{item.title}}</p>
                    </div>
                    <div class="price_market clearfix">
                        <p class="stock">库存：{{item.stock}}<strike class="market fr">￥{{item.price_market}}</strike></p>                
                        <p class="price fl">￥<span>{{item.price_member}}</span></p>
                        <span class="love fr" :class="{active:item.is_collect}" @click="collect(index,item)"></span>
                    </div>
                </li>
            </ul>
            <div class="text-center statu" v-show="load_status">{{page_text}}</div>
            <div class="no_goods" v-show="show">暂无商品</div>
        </div> 
        <div class="load" v-show="load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div>
        <div class="load-wrap" v-show="$store.state.class_load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui';
    import QS from 'qs';
    export default {
        name : 'classification',
        data(){
            return {
                status:[true,false],
                load:false,
                scrollWatch:true,
                data:'',
                show:false,
                load_status:false,
                page:1,
                page_text:'加载中...'
            }
        },
        components:{
          
        },
        methods:{
            //收藏
            collect(index,item){
                if(!sessionStorage.getItem('user_ID')){ 
                    Toast('请先登录');
                    this.$router.push('/LogoIn');
                }
                if(item.is_collect == 0){
                    this.type = 1;
                    this.axios.post(API_URL + 'Home/Cart/add_collection',QS.stringify({
                        app_user_id:sessionStorage.getItem('user_ID'),
                        goods_id:item.id,
                        type:this.type
                    })).then((res) => {
                        if (res.data.msg == '收藏成功') {
                            this.data[index].is_collect = 1;
                        }else{
                            Toast({
                                message: res.data.msg,
                                position: 'middle',
                                duration: 800
                            });
                        }
                        Toast({
                            message: res.data.msg,
                            position: 'middle',
                            duration: 800
                        });
                    }).catch((err) => {
                        console.log(err);
                    })
                    
                }else{
                    this.type = 0;
                    this.axios.post(API_URL + 'Home/Cart/add_collection',QS.stringify({
                        app_user_id:sessionStorage.getItem('user_ID'),
                        goods_id:item.id,
                        type:this.type
                    })).then((res) => {
                        if (res.data.msg == '已取消') {
                            this.data[index].is_collect = 0;
                        }else{
                            Toast({
                                message: res.data.msg,
                                position: 'middle',
                                duration: 800
                            });
                        }
                        Toast({
                            message: res.data.msg,
                            position: 'middle',
                            duration: 800
                        });
                    }).catch((err) => {
                        console.log(err);
                    })
                }
            },
            // 商品详情
            tolink(index){
                this.$router.push({
                    name:'product',
                    params:{
                        id:this.data[index].id,
                        status:1
                    }
                });
            }
            
        },
        created() {
            //昨日尾单数据
            this.axios.post(API_URL+'Home/Index/yesterday',QS.stringify({
                        app_user_id:sessionStorage.getItem('user_ID'),
                        page : this.page
                    })).then((res) => {
                        if(res.data.status == 0){
                                this.show = true;
                           }else{
                                this.show = false;
                           }
                                this.$store.state.load_wrap = false;
                                this.data = res.data.data.yesterday_clear;
                    }).catch((err) => {
                        console.log(err);
                    })
        },
        destroyed(){
            this.scrollWatch = false;
        },
        mounted() {
              document.body.scrollTop = 0;
              let this_ = this;
              let sw = true;
                // 注册scroll事件并监听
                window.addEventListener('scroll',() =>{
                    if(this_.scrollWatch){
                        // 判断是否滚动到底部
                        if(document.body.scrollTop + window.innerHeight >= document.body.offsetHeight) {
                            if(this.show != true){
                                this.load_status = true;
                            }else{
                                return false;
                            }
                            // 如果开关打开则加载数据
                            if(sw==true) {
                                this_.page +=1;
                                sw = false;
                                if(this_.page_text === '暂无更多数据'){
                                        sw = true;
                                        return false;
                                }else{
                                     this.axios.post(API_URL+'Home/Index/yesterday',QS.stringify({
                                            app_user_id:sessionStorage.getItem('user_ID'),
                                            page : this.page
                                        })).then((res) => {
                                            sw = true;
                                               if(res.data.status === 0){
                                                    this_.page_text = '暂无更多数据';
                                                    return false;
                                                }
                                                res.data.data.yesterday_clear.forEach(function(val,index){
                                                    this_.data.push(val);
                                                   });
                                        }).catch((err) => {
                                            console.log(err);
                                        })

                                }
                            
                            }
                        }
                    }

                });
           
        },
        updated(){
            var that = this;
            setTimeout(function(){
                that.$store.state.class_load = false;
            },500);
        },
        destroyed(){
            this.scrollWatch = false;
        },
    }
</script>
<style lang="less" scoped>
    .classif-wrap{
        background:#f2f2f2;
        .header {
            background: #ed3851;
            text-align: center;
            color: #fff;
            font-size: .36rem;
            font-weight: 700;
            line-height: .62rem;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: .62rem;
            padding: .16rem 0;
        }
        .hotGoods-wrap{
            background:#f2f2f2;
            padding-top:.94rem;
            .sold {
                height:1rem;
                overflow:hidden;
                .sold_time {
                    background:#fff;
                    width:100%;
                    height:1.6rem;
                    overflow:hidden;
                    overflow-x:auto;
                    ul {
                        height:1.6rem;
                        li {
                            text-align:center;
                            width:1.45rem;
                            height:1rem;
                            line-height:1rem;
                            padding:0 .15rem;
                            font-size:.3rem;
                            color:#727171;
                            font-weight:500;
                        }
                        li.active{
                           color:#ed3851;
                        }
                    }
                }
            }
            
            .content-main{
                li{
                    width:3.7rem;
                    background:#fff;
                    margin-top:.1rem;
                    margin-right:.08rem;
                    .image{
                        width:3.7rem;
                        height:3.7rem;
                        img {
                            width:100%;
                            height:100%;
                        }
                        .sellout_img{
                            position:absolute;
                            width:2.5rem;
                            height:2.5rem;
                            top:17%;
                            left:17%;
                        }
                    }
                    .desi{
                        line-height:normal;
                        font-size:.3rem;
                        color:#333;
                        padding:.2rem;
                        overflow : hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        border-top:1px solid #f2f2f2;
                        box-sizing: border-box;
                        padding-bottom: 0;
                    }
                    .price_market {
                        padding:.2rem;
                        .stock {
                            font-size: .24rem;
                            color: #aeadad;
                            padding-bottom:.1rem;
                        }
                       .price{
                            line-height:normal;
                            color:#e02828;
                            font-size:.25rem;
                            span{
                                font-size:.36rem;
                            }
                        } 
                        .love {
                            width:.5rem;
                            height:.5rem;
                            background:url(../../assets/love.png) no-repeat;
                            background-size:100% 100%;
                        }
                        .love.active {
                            background:url(../../assets/love01.png) no-repeat;
                            background-size:100% 100%;
                        }
                    }
                    
                }
                li:nth-child(2n){
                    margin-right:0;
                }
            }
             .statu{
                padding:.2rem 0;
                font-size:.3rem;
                color:#757575;
            }
            .no_goods {
                height:77%;
                padding-top:1rem;
                color:#727171;
                font-size:.3rem;
                text-align:center;
                background:#fff;

            }
        }
        
    }
</style>


